<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Datos</h4>
		<b-form-group label="*Tipo"
					  label-for="tipo"
					  @submit.stop.prevent="handleSubmit">
			<b-form-input id="tipo"
						  v-model="item_med.tipo"
						  type="text"
						  placeholder="Ingrese el tipo"
						  invalid-feedback="Complete este campo"
						  :state="validacion.tipo.estado"
						  required>
			</b-form-input>
			<b-form-invalid-feedback id="tipo-live-feedback">
				{{ validacion.tipo.mensaje }}
			</b-form-invalid-feedback>
		</b-form-group>

		<b-form-group label="*Tipo de Suelo"
					  label-for="tiposuelo"
					  @submit.stop.prevent="handleSubmit">
			<b-form-input id="tiposuelo"
						  v-model="item_med.tiposuelo"
						  type="text"
						  placeholder="Ingrese el tipo de suelo"
						  invalid-feedback="Complete este campo"
						  :state="validacion.tiposuelo.estado"
						  required>
			</b-form-input>
			<b-form-invalid-feedback id="tiposuelo-live-feedback">
				{{ validacion.tiposuelo.mensaje }}
			</b-form-invalid-feedback>
		</b-form-group>

		<b-form-group label="*Campo"
					  label-for="id_campo"
					  @submit.stop.prevent="handleSubmit">
			<v-autocomplete id="id_campo"
							v-model="item_med.id_campo"
							invalid-feedback="Complete este campo"
							required
							solo
							filled
							:state="validacion.id_campo.estado"
							:items="options">
			</v-autocomplete>
			<b-form-invalid-feedback id="id_campo-live-feedback">
				{{ validacion.id_campo.mensaje }}
			</b-form-invalid-feedback>
		</b-form-group>

		<b-form-group label="*Porcentaje de humedad aprovechable"
					  label-for="humedad"
					  @submit.stop.prevent="handleSubmit">
			<b-form-input id="humedad"
						  v-model="item_med.humedad"
						  type="text"
						  :state="validacion.humedad.estado"
						  placeholder="Ingrese un numero"
						  invalid-feedback="Complete este campo"
						  aria-describedby="input-live-help input-live-feedback"
						  required>
				<b-form-invalid-feedback id="humedad-live-feedback">
					Debe ingresar al menos 1 numero.
				</b-form-invalid-feedback>
			</b-form-input>
			<b-form-invalid-feedback id="humedad-live-feedback">
				{{ validacion.humedad.mensaje }}
			</b-form-invalid-feedback>
		</b-form-group>

		<b-form-group label="*Coordenadas X"
					  label-for="coordenadasX"
					  @submit.stop.prevent="handleSubmit">
			<b-form-input id="coordenadasX"
						  v-model="item_med.coordenadasX"
						  type="number"
						  :state="validacion.coordenadasX.estado"
						  placeholder="Ingrese un numero"
						  invalid-feedback="Complete este campo"
						  aria-describedby="input-live-help input-live-feedback"
						  required>
				<b-form-invalid-feedback id="coordenadasX-live-feedback">
					Debe ingresar al menos 1 numero.
				</b-form-invalid-feedback>
			</b-form-input>
			<b-form-invalid-feedback id="coordenadasX-live-feedback">
				{{ validacion.coordenadasX.mensaje }}
			</b-form-invalid-feedback>
		</b-form-group>
		<b-form-group label="*Coordenadas Y"
					  label-for="coordenadasY"
					  @submit.stop.prevent="handleSubmit">
			<b-form-input id="coordenadasY"
						  v-model="item_med.coordenadasY"
						  type="number"
						  :state="validacion.coordenadasY.estado"
						  placeholder="Ingrese un numero"
						  invalid-feedback="Complete este campo"
						  aria-describedby="input-live-help input-live-feedback"
						  required>
				<b-form-invalid-feedback id="coordenadasY-live-feedback">
					Debe ingresar al menos 1 numero.
				</b-form-invalid-feedback>
			</b-form-input>
			<b-form-invalid-feedback id="coordenadasY-live-feedback">
				{{ validacion.coordenadasY.mensaje }}
			</b-form-invalid-feedback>
		</b-form-group>

		<b-button class="mt-2" variant="success" block @click="putCultivo()">Guardar</b-button>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";
	import axios from "axios";
	1;
	export default {
		props: {
			item_med: {},
			updateTable: Function,
		},
		data() {
			return {
				// medicamentos: {},
				proveedores: {},
				data: {},
				respuesta: {},
				options: [{ value: null, text: "Elija un campo", disabled: true }],
				validacion: {
					id_cultivo: { estado: null, mensaje: "" },
					tipo: { estado: null, mensaje: "" },
					id_campo: { estado: null, mensaje: "" },
					humedad: { estado: null, mensaje: "" },
                    tiposuelo: { estado: null, mensaje: "" },
					coordenadasX: { estado: null, mensaje: "" },
                    coordenadasY: { estado: null, mensaje: "" },
				},
			};
		},
		created: function () {
			//console.log('Funcion realizada');
			this.getCampos();
		},
		methods: {
            async getCampos() {
                let proveedorAPI = new APIControler();
                proveedorAPI.apiUrl.pathname = "campos/";
                this.data = await proveedorAPI.getData(this.campos);
                this.data.forEach((element) => {
                    let option = {};
                    option.value =
                        "https://45.162.168.170:8081/campos/" + element.id_campo + "/";
                    option.text = element.id_campo + " - " + element.nombre + " - " + element.calle;
                    this.options.push(option);
                });
            },

			async putCultivo() {
				let resp = "vacio";
				await axios
					.put(
						"http://45.162.168.170:8081/cultivos/" +
							this.item_med.id_cultivo +
							"/",
						this.item_med
					)
					.then(function () {
						swal("Operación Exitosa", " ", "success");
					})
					.catch(function (error) {
						const mje =
							error.response.status < 500
								? "Los datos no son válidos"
								: "Se ha detectado un problema ";
						swal("¡ERROR!", mje, "error");
						resp = error.response.data;
					});
				this.respuesta = resp;
				this.cargarFeedback();
				this.updateTable();
			},
			cargarFeedback() {
				let valido;
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
		},
        beforeMount() {
            this.getCampos();
        },
	};
</script>

<style></style>
