				<b-form-input id="coefmarch"
							  v-model="cultivos.coefmarch"
							  type="text"
							  :state="validacion.coefmarch.estado"
							  placeholder="Ingrese un numero"
							  invalid-feedback="Complete este campo"
							  aria-describedby="input-live-help input-live-feedback"
							  required>
					<b-form-invalid-feedback id="coefmarch-live-feedback">
						Debe ingresar al menos 1 numero.
					</b-form-invalid-feedback>
				</b-form-input>
				<b-form-invalid-feedback id="coefmarch-live-feedback">
					{{ validacion.coefmarch.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Densidad Aparente"
						  label-for="densidadap"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="densidadap"
							  v-model="cultivos.densidadap"
							  type="text"
							  :state="validacion.densidadap.estado"
							  placeholder="Ingrese un numero"
							  invalid-feedback="Complete este campo"
							  aria-describedby="input-live-help input-live-feedback"
							  required>
					<b-form-invalid-feedback id="densidadap-live-feedback">
						Debe ingresar al menos 1 numero.
					</b-form-invalid-feedback>
				</b-form-input>
				<b-form-invalid-feedback id="densidadap-live-feedback">
					{{ validacion.densidadap.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Coeficiente Agotamiento"
						  label-for="coefagot"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="coefagot"
							  v-model="cultivos.coefagot"
							  type="text"
							  :state="validacion.coefagot.estado"
							  placeholder="Ingrese un numero"
							  invalid-feedback="Complete este campo"
							  aria-describedby="input-live-help input-live-feedback"
							  required>
					<b-form-invalid-feedback id="coefagot-live-feedback">
						Debe ingresar al menos 1 numero.
					</b-form-invalid-feedback>
				</b-form-input>
				<b-form-invalid-feedback id="coefagot-live-feedback">
