				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Profundidad de Raiz"
						  label-for="profraiz"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="profraiz"
							  v-model="cultivos.profraiz"
							  type="text"
							  :state="validacion.coefagot.estado"
							  placeholder="Ingrese un numero"
							  invalid-feedback="Complete este campo"
