				<b-form-input id="coordenadasX"
							  v-model="cultivos.coordenadasX"
							  type="number"
							  :state="validacion.coordenadasX.estado"
							  placeholder="Ingrese un numero"
							  invalid-feedback="Complete este campo"
							  aria-describedby="input-live-help input-live-feedback"
							  required>
					<b-form-invalid-feedback id="coordenadasX-live-feedback">
						Debe ingresar al menos 1 numero.
					</b-form-invalid-feedback>
				</b-form-input>
				<b-form-invalid-feedback id="coordenadasX-live-feedback">
					{{ validacion.coordenadasX.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Coordenadas Y"
						  label-for="coordenadasY"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="coordenadasY"
							  v-model="cultivos.coordenadasY"
							  type="number"
							  :state="validacion.coordenadasY.estado"
							  placeholder="Ingrese un numero"
							  invalid-feedback="Complete este campo"
							  aria-describedby="input-live-help input-live-feedback"
							  required>
