				</b-form-invalid-feedback>
			</b-form-group>

		</b-form>
		<b-button class="mt-2" variant="success" block @click="postCultivo()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";

	export default {
		props: {
			updateTable: Function,
		},
		data() {
			return {
				cultivos: {},
				cultivos2: {},
                cultivos3: {},
				respuesta: {},
				proveedores: {},
				data: {},

				options: [{ value: null, text: "Elija un campo", disabled: true }],

				text: "",
				validacion: {
					id_cultivo: { estado: null, mensaje: "" },
					tipo: { estado: null, mensaje: "" },
                    id_campo: { estado: null, mensaje: "" },
					humedad: { estado: null, mensaje: "" },
					coefmarch: { estado: null, mensaje: "" },
					coefagot: { estado: null, mensaje: "" },
					densidadap: { estado: null, mensaje: "" },
                    profraiz: { estado: null, mensaje: "" },
                    tiposuelo: { estado: null, mensaje: "" },
					coordenadasX: { estado: null, mensaje: "" },
                    coordenadasY: { estado: null, mensaje: "" },
				},
			};
		},

		methods: {
			async getCampos() {
				let proveedorAPI = new APIControler();
                proveedorAPI.apiUrl.pathname = "campos/";
                this.data = await proveedorAPI.getData(this.campos);
				this.data.forEach((element) => {
					let option = {};
					option.value =
                        "https://45.162.168.170:8081/campos/" + element.id_campo + "/";
                    option.text = element.id_campo + " - " + element.nombre + " - " + element.calle;
                    this.options.push(option);
				});
			},
			/*
    async getMedicamento() {
      let medicamentosAPI = new APIControler();
      this.data = await medicamentosAPI.getData();
    },*/

			async postCultivo() {
				let cultivosAPI = new APIControler();
				cultivosAPI.apiUrl.pathname = "cultivos/";
                this.respuesta = await cultivosAPI.postData(this.cultivos);
				//this.data = await medicamentosAPI.postData(this.medicamentos);
				this.cargarFeedback();
				this.resetForm();

				this.updateTable();
			},

			async resetForm() {
                this.cultivos.id_cultivo = null;
                this.cultivos.nombre = "";
                this.cultivos.tipo_cultivo = "";
                this.cultivos.letra = "";
                this.cultivos.numero_cultivo = "";
			},

			cargarFeedback() {
				let valido;
				if (!this.respuesta) this.respuesta = {};
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
		},
		beforeMount() {
			this.getCampos();
		},
		/*
  computed: {
      validation() {
        return this.text.length > 0 ? true : false;
      },
    }
  */
	};
</script>

<style></style>
